@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans Regular"),
    local("OpenSans-Regular"),
    url("../fonts/open-sans-v17-latin_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/open-sans-v17-latin_cyrillic-regular.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Open Sans Light"),
    local("OpenSans-Light"),
    url("../fonts/open-sans-v17-latin_cyrillic-300.woff2") format("woff2"),
    url("../fonts/open-sans-v17-latin_cyrillic-300.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Open Sans SemiBold"),
    local("OpenSans-SemiBold"),
    url("../fonts/open-sans-v17-latin_cyrillic-600.woff2") format("woff2"),
    url("../fonts/open-sans-v17-latin_cyrillic-600.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans Bold"),
    local("OpenSans-Bold"),
    url("../fonts/open-sans-v17-latin_cyrillic-700.woff2") format("woff2"),
    url("../fonts/open-sans-v17-latin_cyrillic-700.woff") format("woff");
}
